import React, { Component } from 'react';
import { render } from 'react-dom';
import { ApolloProvider, Query } from 'react-apollo';
import gql from 'graphql-tag';
//import ApolloClient from 'apollo-boost';
import { ApolloClient } from 'apollo-client';
import { createHttpLink } from 'apollo-link-http';
import { InMemoryCache } from 'apollo-cache-inmemory';
import Select from 'react-select';
import styled from 'styled-components';

const client = new ApolloClient({
  link: createHttpLink({ uri: 'http://nlbavwixs.infor.com:4000' }),
  cache: new InMemoryCache()
});

const QUERY_SUPPORT_FOLKS = gql`
  query QUERY_SUPPORT_FOLKS {
    newguests {
      id
      login
      firstname
      lastname
      navid
    }
    supportfolks {
      fullname
      image
      navid
    }
  }
`;

const StyledSelect = styled(Select)`
  font-family: Roboto;
`;

const Avatar = styled.img`
  width: 48px;
  height: 48px;
  border-radius: 50%;
`;

const AvImg = styled.div`
  font-size: 20px;
  font-weight: 800;
  width: 48px;
  height: 48px;
  font-family: Roboto;
  border-radius: 50%;
  background: grey;
  color: white;
`;

const Wrapper = styled.div`
  display: flex;
  margin: 5px;
`;

console.log(Query);
client.query({ query: QUERY_SUPPORT_FOLKS }).then(result => console.log(result));

function Test(props) {
  const {
    data: { value, label, image }
  } = props;
  const initials = label
    .split(' ')
    .map(l => l.slice(0, 1))
    .join('')
    .toUpperCase();
  return (
    <Wrapper>
      {image ? <Avatar src={image} /> : <AvImg>{initials}</AvImg>}
      {label}
    </Wrapper>
  );
}

class Index extends Component {
  render() {
    return (
      <Query query={QUERY_SUPPORT_FOLKS}>
        {({ data, loading, error }) => {
          console.log('data');
          if (loading) return 'Loading';
          if (error) return 'error';
          //return <div>Hallo</div>;
          console.log('data', data);
          const { supportfolks, newguests } = data;
          const sfoptions = supportfolks.map(person => ({
            value: person.navid,
            label: person.fullname,
            image: person.image
          }));
          const options = newguests.map(guest => ({
            value: guest.navid,
            label: guest.firstname + ' ' + guest.lastname
          }));

          return (
            <div>
              <StyledSelect
                placeholder="Select a new user from the list"
                options={options}
                onChange={e => console.log(e)}
              />
              <StyledSelect
                placeholder="Select a new user from the list"
                options={sfoptions}
                components={{ Option: Test }}
                onChange={e => console.log(e)}
              />
            </div>
          );
        }}
      </Query>
    );
  }
}

const x = () => (
  <Query query={QUERY_SUPPORT_FOLKS}>
    {({ data, loading, error }) => {
      console.log('data');
      if (loading) return 'Loading';
      if (error) return 'error';
      return <div>Hallo</div>;
      console.log('data', data);
      return <div>{JSON.stringify(data.supportfolks)}</div>;
    }}
  </Query>
);

function App() {
  return (
    <ApolloProvider client={client}>
      <Index />
    </ApolloProvider>
  );
}

render(<App />, document.getElementById('rootNewIXSUser'));
